import { Component, h, Prop, Element } from '@stencil/core';

@Component({
  tag: 'ks-info-message',
  styleUrl: 'info-message.css'
})
export class InfoMessage {
    @Element() root: HTMLElement;
    @Prop() name: string;
    @Prop({mutable: true, reflect: true}) disabled: boolean = false;
    @Prop({mutable: true, reflect: true}) animating: boolean = false;

    id: string = "";
    componentWillLoad() {
        this.id = "ks-info-message-" + this.name.replace(" ", "");

        if (sessionStorage.getItem(this.id) == "true")
            this.disabled = true;
    }
    
    disable() {
        sessionStorage.setItem(this.id, "true");
        this.animating = true;
        setTimeout(() => {
            this.disabled = true;
        }, 300);
    }

    render() {
        return[
            <div class="content">
                <slot></slot>
            </div>,
            <div class="close">
                <ks-icon name="x" onClick={()=>this.disable()}></ks-icon>
            </div>
        ];
    }
}